.header-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.icon-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.header-bascket-btn {
  height: 24px;
  padding: 0;
}

.icon-menu {
  stroke: var(--main-color);
}

.header-logo-main {
  color: var(--main-color);
}

.header-logo-part {
  color: var(--active-color);
}

.bascket-icon {
  fill: rgba(0, 0, 0, 0);
  stroke: var(--main-color);
  transition: stroke var(--transition-duration) var(--transition-time-function);
}

.bascket-icon:hover, .bascket-icon:focus {
  stroke: var(--active-color);
}

.header-icon-wrapper {
  display: flex;
}

.header-logo {
  letter-spacing: -.64px;
  padding-top: 27, 5px;
  padding-bottom: 27, 5px;
  transition: text-decoration var(--transition-duration) var(--transition-time-function);
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.13;
}

.header-logo:hover, .header-logo:focus {
  text-decoration: underline;
}

.nav-list {
  align-items: center;
  gap: 16px;
  display: flex;
}

.checkbox {
  width: 46px;
  height: 20px;
  border-radius: 10px;
  align-items: center;
  display: flex;
  position: relative;
}

.checkbox-inner {
  cursor: pointer;
  background-color: var(--additional-color);
  width: 46px;
  height: 20px;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .25));
  z-index: 1000;
  border-radius: 35px;
  position: absolute;
  top: 0;
  left: 0;
}

.checkbox-inner:before {
  content: "";
  width: 18.8px;
  height: 18.8px;
  background-color: var(--secondary-color);
  z-index: 100;
  transition: background-color var(--transition-duration) var(--transition-time-function) transform var(--transition-duration) var(--transition-time-function);
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.checkbox-input:checked + .checkbox-inner:before {
  background-color: #fff;
  transform: translateX(26px);
}

.checkbox-input:checked + .checkbox-inner {
  background-color: var(--active-color);
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .25));
}

@media screen and (max-width: 767px) {
  .mobile-menu.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }

  .header-icon-wrapper {
    align-items: center;
    gap: 14px;
  }

  .nav-menu, .switch-checkbox-wrapper {
    display: none;
  }

  .burger-menu {
    height: 32px;
    padding-left: 0;
    padding-right: 0;
  }

  .mobile-close-icon {
    stroke: var(--secondary-rgb-color);
    position: absolute;
    top: 20px;
    right: 20px;
  }

  .mobile-menu {
    width: 50%;
    height: 100vh;
    z-index: 100;
    background-color: var(--active-color);
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: opacity var(--transition-duration) var(--transition-time-function) visibility var(--transition-duration) var(--transition-time-function);
    padding-top: 200px;
    position: fixed;
    top: 0;
    left: 0;
  }

  .mobile-menu-close-btn {
    height: 32px;
    display: block;
  }

  .mobile-nav-list {
    flex-direction: column;
    align-items: center;
    gap: 16px;
    display: flex;
  }

  .mobile-nav-link {
    color: var(--secondary-color);
    transition: color var(--transition-duration) var(--transition-time-function), border-bottom var(--transition-duration) var(--transition-time-function);
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.2;
  }

  .mobile-nav-link:hover, .mobile-nav-link:focus {
    color: #fff;
    border-bottom: 1px solid #fff;
  }

  .mobile-nav-link.current {
    color: #fff;
    font-weight: 500;
  }

  .mobile-menu {
    transition: transform var(--transition-duration) var(--transition-time-function);
    transform: translateX(-100%);
  }

  .mobile-menu.is-open {
    transform: translateX(0);
  }

  .checkbox {
    position: absolute;
    bottom: 200px;
    left: 20px;
  }
}

